import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

// SERVICES
import { DatabaseService } from 'src/app/services/database.service';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html'
})
export class SidebarComponent implements OnInit {

  books:any;

  // TODO: FIXME: hay que crear estas datas en archivos aparte para mantener el orden.
  categories:Array<string>=["Antiguedades y Coleccionables", "Arquitectura", "Arte", "Artes Escnicas", "Biografa y Autobiografa", "Casa y Hogar", "Ciencia", "Ciencias Polticas", "Ciencias Sociales", "Cocina", "Comida y Bebestibles", "Colecciones Literarias", "Cmics y Novelas Grficas", "Computacin e Internet", "Crmenes", "Crtica Literaria", "Cuerpo", "Mente y Espritu", "Deportes y Recreacin", "Drama", "Educacin", "Estudio de Lenguas Extranjeras", "Ensayos Acadmicos", "Familia y Relaciones", "Ficcin", "Ficcin Adolescente", "Ficcin para Nios", "Filosofa", "Fotografa", "Historia y Geografa", "Humor", "Jardinera", "Juegos", "Lectura escolar", "Lengua y Literatura", "Leyes", "Manualidades y Hobbies", "Mascotas y Animales", "Matemticas", "Medicina", "Msica", "Naturaleza y Aire libre", "Negocios y Economa", "Nios y Jvenes", "Papelera", "Poesa", "Psicologa", "Religin y Espiritualidad", "Salud y Bienestar", "Tecnologa", "Transporte", "Viajes"];

  constructor( private _dbService: DatabaseService, private router: Router ) { }

  ngOnInit() {
    this._dbService.getData('books')
		  .valueChanges()
			.subscribe( data => this.books = data );
  }

  // Busca los libros segn el search, que es la categora.
  searchCategory( search:string ){
    let searchCategories = [];

    for( let book of this.books ) {
      let categories = book.genres;
      let flag = false; // con esto valido que se agregue solo 1 vez el mismo libro
      
      for( let category of categories ){
        let cat = category.toLowerCase();

        if( cat.indexOf(search.toLowerCase()) >= 0 && !flag ) {
          flag = true;
          searchCategories.push(book);
        }
      }
    }
    this.router.navigate(['/search', search]);
  }

}
